const container = document.getElementById('root');
const ajax = new XMLHttpRequest();
const content = document.createElement('div');
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';

const store = {
    currentPage: 1,
}

//Î∞òÎ≥µÏΩîÎìú getDateÌï®ÏàòÏïàÏóê ajax ÏΩîÎìúÎ•º ÎÑ£Í≥† urlÎ∞îÎÄîÏàò ÏûàÏñ¥ Ïù∏Ïûê Í∞íÏóê urlÏùÑ Ï§çÎãàÎã§
function getDate(url){
    ajax.open('GET', url , false);
    ajax.send();

    //ajax.response Î∞òÌôòÍ∞í return
    return JSON.parse(ajax.response);
};

//ajax getDate Ìï®ÏàòÎ•º ÌÜµÌï¥ÏÑú Î∂àÎü¨ÏôÄÏÑú ÏΩîÎìúÎ•º Ï§ÑÏù∏Îã§

const ul = document.createElement('ul');

//Î¨∏ÏûêÏó¥Î°ú html ÎßåÎì§Í∏∞ ÎêòÎèÑÎ°ù DOMÍ∞ùÏ≤¥Ïóê ElementÏÜçÏÑ±ÏùÑ ÏïàÏì∞Í≥† UIÎ•º Íµ¨ÏÑ±ÌïòÎäîÍ≤ÉÏù¥ Ìö®Ïú®Ï†ÅÏù¥Í≥† Ï¢ãÏùå 
//ÏßÄÍ∏àÏùÄ Ïó∞ÏäµÏù¥ÎùºÏÑú ÏΩîÎìúÎ•º Í≥ÑÏÜç ÎßåÎì§Ïñ¥Î≥¥ÎäîÍ≤ÉÏûÖÎãàÎã§!

function newsFeeds(){
    const newsFeed = getDate(NEWS_URL)
    
const newsList = [];
    newsList.push(`<ul>`)
    for(let i = (store.currentPage -1) * 10; i < store.currentPage * 10; i++) {
        const div = document.createElement('div');
        newsList.push(`  
            <li>
                <a href="#${newsFeed[i].id}"> 
                ${newsFeed[i].title} (${newsFeed[i].comments_count})       
                </a>
            </li>
        `);
    }
    newsList.push(`</ul>`);
    newsList.push(`
        <div>
            <a href="#/page/${store.currentPage > 1 ? store.currentPage -1 : 1}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
            <a href="#/page/${store.currentPage + 1}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
        </div>
    `)

    container.innerHTML = newsList.join('')
}

function newsDetail(){
        const id = location.hash.substr(7);
        const newsContent = getDate(CONTENT_URL.replace('@id' , id));
        container.innerHTML = `
                <h1>${newsContent.title}</h1>
                <a href="/">
                    Î™©Î°ùÏúºÎ°ú
                </a>
                `
}

function router(){
    const routerPath = location.hash;

    if(routerPath === ""){
        newsFeeds();
    }else if(routerPath.indexOf('#/page/') >= 0){
        store.currentPage = Number(routerPath.substr(7));
        newsFeeds();
    }else{
        newsDetail();
    }
}

window.addEventListener('hashchange', router)
router();